import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import ColorUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/color/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/colors"

class UpdateColor extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editColor } = this.props;
    const { id } = this.props.match.params;
    editColor(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.color.error) {
      notification("error", this.props.color.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.color.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateColor } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //  value.location_code = value.location_code.toUpperCase();
    updateColor(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="colors.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.color.loadingEdit ? (
            <div className="spinStyle">
              <Spin color="large" />
            </div>
          ) : (
              <ColorUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                color={this.props.color}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    color: state.color
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateColor);
