const actions = {
  getColors: (page, search, filter) => ({
    type: 'GET_COLORS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createColor: () => ({
    type: 'CREATE_COLOR_REQUEST'
  }),
  storeColor: data => ({
    type: 'STORE_COLOR_REQUEST',
    payload: { body: data }
  }),
  editColor: id_color => ({
    type: 'EDIT_COLOR_REQUEST',
    payload: { id_color: id_color }
  }),
  updateColor: (id_color, data) => ({
    type: 'UPDATE_COLOR_REQUEST',
    payload: {
      id_color: id_color,
      body: data
    }
  }),
  showColor: id_color => ({
    type: 'SHOW_COLOR_REQUEST',
    payload: { id_color: id_color }
  }),
  deactivateColor: id_color => ({
    type: 'DEACTIVATE_COLOR_REQUEST',
    payload: { id_color }
  }),
  activateColor: id_color => ({
    type: 'ACTIVATE_COLOR_REQUEST',
    payload: { id_color }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
